export default {
  data() {
    return {
      fileList: [],
      input: "",
      primaryValue: "",
      secondValue: "",
      primaryClassification: [],
      secondClassification: [],
      textarea: "",
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },

    handlePreview(file) {
      console.log(file);
    },

    handleSuccess(response, file, fileList) {
      if (response.status) {
        console.log(this.fileList);
      } else {
        this.$notify.error({
          title: "错误",
          message: response.message,
          duration: 0,
        });
      }
    },

    primarySelectChange() {
      let params = new URLSearchParams();
      params.append("primaryClassification", this.primaryValue);
      this.axios
        .post("/getSecondClassificationByPrimaryClassification", params)
        .then((res) => {
          let result = res.data;
          if (result.status) {
            this.secondClassification = result.list;
          } else {
            this.$notify.error({
              title: "错误",
              message: result.message,
              duration: 0,
            });
          }
        })
        .catch((err) => {
          this.$notify.error({
            title: "错误",
            message: err,
            duration: 0,
          });
        });
    },

    handleCommit() {},

    submitUpload() {
      this.$refs.upload.submit();
    },
  },
  created() {
    this.axios
      .get("/primaryClassification/getAll")
      .then((res) => {
        this.primaryClassification = res.data;
      })
      .catch((err) => {
        this.$notify.error({
          title: "错误",
          message: err,
          duration: 0,
        });
      });
  },
};



